import React, {Component} from 'react';
import {
    StyleSheet,
    Text,
    View,
    TouchableOpacity,
    Image,
    TouchableWithoutFeedback,
    Keyboard, ScrollView
} from 'react-native';
import Colors from "../../utils/Colors";
import PhonePresenter from "./presenter/PhonePresenter";
import {isValidPhoneNumber}  from 'react-phone-number-input'
import {Snackbar} from "react-native-paper";
import BackButton from "../../components/BackButton";
import languages from "../../utils/languages/AppLocalization";
import * as Localization from "expo-localization";
import CustomPhoneInput from "../../components/phone/PhoneInput";

class PhoneScreen extends Component {

    constructor(props) {
        super(props)
        this.presenter = new PhonePresenter()
        this.presenter.setView(this)
        this.state = {
            enableToSend: false,
            phoneError: false,
            errorBackend: false,
            snackColor: Colors.red,
            message: '',
            phone:'',
            country:null,
        }
    }

    showBackendError() {
        this.setState({
            errorBackend: true,
            message: ' An error did happen please try again later.',
            snackColor: Colors.red
        })
    }

    navigateToCode() {
        this.props.navigation.navigate('Code')
    }

    _onCallBack(country){
        this.setState({country: country})
    }

    render() {
        let color = this.state.enableToSend ? Colors.buttonEnable : Colors.disable
        return <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
            <View style={styles.container}>

                <BackButton onClick={() => {
                    this.props.navigation.goBack()
                }}/>

                <Image
                    style={styles.icon}
                    source={require('../../../../assets/logo.png')}/>

                <ScrollView style={styles.scrollView} contentContainerStyle={styles.container_scrollView}>

                    <Text style={styles.title}>{languages.getLocalized("phone_title")}</Text>

                    <Text style={styles.subTitle}>{languages.getLocalized("phone_subtitle")}</Text>

                    <View style={styles.textInputContainer}>
                        <CustomPhoneInput country={this.state.country}
                                          currentCountry={Localization.region} onChangeText={(phone => {
                            this.setState({phoneError: !isValidPhoneNumber(phone), enableToSend: isValidPhoneNumber(phone), phone:phone})
                        })} iconPressed={() => this.props.navigation.navigate('CountrySelector', {callback: this._onCallBack.bind(this)})}
                        />

                    </View>
                    {this.state.phoneError ?
                        <Text style={styles.error}>{languages.getLocalized("phone_error")}</Text> : null}

                    <Text style={styles.message}>{languages.getLocalized("phone_secure_message")}</Text>
                </ScrollView>

                <TouchableOpacity disabled={!this.state.enableToSend} activeOpacity={0.7}
                                  style={[styles.button, {backgroundColor: color}]}
                                  onPress={() => this.presenter.savePhone(this.state.phone)}>
                    <Text style={styles.buttonText}>{languages.getLocalized("phone_send_code")}</Text>
                </TouchableOpacity>
                <Snackbar
                    visible={this.state.errorBackend}
                    onDismiss={() => this.setState({errorBackend: false})}
                    duration={3000}
                    style={{backgroundColor: this.state.snackColor}}>
                    {this.state.message}
                </Snackbar>
            </View>
        </TouchableWithoutFeedback>
    }
}

const styles = StyleSheet.create({

    container: {
        justifyContent: 'flex-start',
        alignItems: 'center',
        flex: 1,
        backgroundColor: Colors.white,
    },

    icon: {
        marginTop: 58,
        width: 180,
        height: 55,
    },

    scrollView: {
        width: '100%',
    },

    container_scrollView: {
        justifyContent: 'flex-start',
        alignItems: 'center',
    },

    title: {
        textAlign: "center",
        width: '90%',
        fontSize: 16,
        fontFamily: 'Nunito-Bold',
        marginTop: 25,
        color: Colors.buttonEnable
    },

    subTitle: {
        textAlign: "left",
        width: '90%',
        fontSize: 18,
        fontFamily: 'Nunito-Regular',
        marginTop: 30,
        color: Colors.buttonEnable
    },

    message: {
        textAlign: "left",
        width: '90%',
        fontSize: 14,
        fontFamily: 'Nunito-Regular',
        marginTop: 80,
        color: Colors.buttonEnable
    },

    test:{
        position: 'absolute',
        left:50,
        top:0
    },

    textInputContainer: {
        width: "90%",
        borderBottomWidth: 2,
        borderColor: Colors.primary,
        borderRadius: 4,
        marginTop: 60,
        padding: 5
    },

    textInput: {
        width: "100%",
        height: 35,
    },

    text: {
        color: Colors.textFormItem,
        fontFamily: 'Nunito-Regular',
    },

    errorPhone: {
        width: "90%",
        marginTop: 5,
        fontSize: 12,
        color: Colors.red
    },

    button: {
        justifyContent: 'center',
        width: '90%',
        height: 66,
        padding: 10,
        borderRadius: 6,
        backgroundColor: Colors.buttonEnable,
        position: "absolute",
        bottom: 15,
    },

    buttonText: {
        fontFamily: 'Nunito-Bold',
        textAlign: "center",
        fontSize: 18,
        color: Colors.white,
    },

    error: {
        width: "90%",
        marginTop: 5,
        fontSize: 12,
        color: Colors.red
    },
});



export default PhoneScreen;
